// Tv-home
.tv-home
  z-index: 3
  position: relative
  /* Global*/
  .Procuct-items
    background-color: #f5f5f7
  > .section
    padding: 30px 10%
    background-color: #f5f5f7
    > .title
      margin-bottom: 25px
      display: flex
      > h2
        font-size: 50px
  //section1
  .section1
    display: flex
    flex-direction: column
    .title
      order: 2
      flex-direction: column
      align-items: center
      text-align: center
      h2
        font-size: 80px
      h3
        font-size: 22px
        color: grey
    .video
      order: 1
      height: 450px
      background-image: url(https://www.apple.com/v/tv-home/k/images/overview/hero__dbphk49ymi2q_large.jpg)
      background-position: center
      background-repeat: no-repeat
  //section2
  .section2
    padding: 30px
    display: flex
    justify-content: center
    flex-wrap: wrap
    gap: 30px
    > .product-card
      width: 45%
      height: 750px
      justify-content: space-between
      background: white
      .card-image
        width: 100%
        order: 2
      .card-info
        text-align: center
        order: 1
        padding: 0
        *
          color: black
        .images-box
          width: auto
          height: 60px
          img
            width: auto
        .title
          font-size: 22px
        .subtitle
          font-size: 30px
        .text
          font-size: 20px
          font-weight: 400
        .button-item
          display: flex
          gap: 15px
          justify-content: center
          > *
            cursor: pointer
            padding: 8px 15px
            border-radius: 20px
          .btn:first-child
            background-color: blue
            color: white
          .btn:last-child
            color: blue
            &::after
              content: ' > '
            &:hover
              text-decoration-line: underline
      &:last-child
        .card-info
          .button-item
            .btn
              color: blue
              background: transparent
      &:first-child
        background-color: black
        .card-info
          *
            color: white
  //section3
  .section3
    background-color: #f5f5f7
    > .title
      justify-content: center
    > .section
      padding-right: 10px
      >.action-btns
        justify-content: center
        position: relative
        opacity: 1
        margin-top: 30px
        gap: 15px
        transform: translate(-78px, -30px)
        button
          display: flex
          justify-content: center
          align-items: center
          width: 30px
          height: 30px
          padding: 0
          border: 1px solid black
          background: transparent
          img
            width: 10px
            height: auto
      .product-card.center
        flex: 0 0 350px
        justify-content: space-between
        margin-right: 15px
        transition: all 0.5s
        height: 500px
        background-color: white
        .card-image
          order: 2
          display: grid
          width: auto
          height: 80%
          img
            height: auto
            align-self: center
        .infos-item
          display: flex
          width: 100%
          align-items: center
          justify-content: center
          line-height: 2
          > .card-info
            padding-left: 20px
            text-align: center
            .title
              font-size: 22px
              justify-content: center
              position: relative
              opacity: 1
              margin-top: 30px
              gap: 15px
              transform: translate(-78px, -30px)
            .text
              font-size: 22px
          .button-item
            .btn
              color: blue
              cursor: pointer
              &::after
                content: ' > '
              &:hover
                text-decoration-line: underline
        &:nth-child(5), &:nth-child(6)
          visibility: hidden
  //section4
  .section4
    .title
      flex-direction: column
      align-items: center
      text-align: center
      h3
        color: #0066cc
        cursor: pointer
        &::after
          content: ' > '
        &:hover
          text-decoration-line: underline
    > .product-info-item
      display: flex
      gap: 40px
      justify-content: center
      > .product-card
        width: 30%
        height: 600px
        background-color: white
        > .card-image
          width: 100%
          height: 350px
          order: 2
        > .card-info
          height: 30%
          text-align: center
          gap: 15px
          margin: 0
          padding: 30px 0
          h6.postscript
            color: red
            font-size: 20px
          h5.title
            color: black
            font-size: 1.5rem
          h4.text
            font-size: 1rem
            font-weight: 500
          h6.subtitle
            font-size: 1rem
            cursor: pointer
            color: #0066cc
            order: 4
            &::after
              content: ' > '
            &:hover
              text-decoration-line: underline
        > p
          order: 3
          text-align: center
          font-size: 13px
          color: grey
